export default {
  data() {
    return {
      imageList: ["../../../static/image/index01.png", "../../../static/image/index02.png", "../../../static/image/index03.png"],
      reslutList: [],
      nartitle: []
    };
  },

  onLoad() {
    this.getData();
    this.init();
  },

  components: {
    uniNoticeBar
  },
  methods: {
    share() {
      uni.share({
        provider: "weixin",
        scene: "WXSceneSession",
        type: 1,
        summary: this.ruleTitle + this.ruleContent,
        success: function (res) {
          console.log("success:" + JSON.stringify(res));
        },
        fail: function (err) {
          console.log("fail:" + JSON.stringify(err));
        }
      });
    },

    init(e) {
      console.log("成功");
      uni.request({
        url: this.$serverUrl + '/snatchinfo/snatchLatest',
        success: ret => {
          if (ret.statusCode !== 200) {
            console.log("home---init请求失败", JSON.stringify(ret));
          } else {
            this.nartitle = ret.data.rows;
            console.log("home---init请求成功");
            console.log(JSON.stringify(ret.data));
          }
        },
        fail: res => {
          console.log("home---init访问失败", JSON.stringify(res));
        }
      });
    },

    getData(e) {
      uni.request({
        url: this.$serverUrl + '/publish/dyPublishList',
        success: ret => {
          if (ret.statusCode !== 200) {
            console.log("home---getdata请求失败", JSON.stringify(ret));
          } else {
            console.log("home---getdata请求成功", JSON.stringify(ret));
            this.reslutList = ret.data.data;
          }
        },
        fail: res => {
          console.log("home---getdata访问失败", JSON.stringify(res));
        }
      });
    },

    jumpDetail(rulePublishId) {
      uni.navigateTo({
        url: 'homeDetail' + '?rulePublishId=' + rulePublishId
      });
    }

  },

  onShow() {}

};